let boards = ((names, data) => {
    var ret = new Map();
    var i;
    for(i = 0; i < boardNames.length; i++){
        const str = randomArray(data[i], 3).join('');
        ret.set(names[i], {data: data[i], preview: str});
    }
    return ret;
})(boardNames,emojiArrays);


// creates a no duplicate randomArray of size n from data
function randomArray(data, n){
    var arr = [];
    var i = 0;
    while(i < n){
        var e = data[Math.floor(Math.random()*data.length)];
        if(arr.includes(e)){
            continue;
        }else{
            arr.push(e);
            i++;
        }
    }
    return arr;
}

